body {
  margin:10px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  margin:10px;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
#todo-page{
    background-color: rgb(161, 167, 167);
  position: absolute;
  height: 98%;
  width: 98%;
  border:1px solid black;
}
#todo-bar{
  width:100%;
  height: 100%;
  height: 97vh;
  border: 1px solid black;
}
.tag:hover{
   background-color: white;
   width:30%;
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f0f0f0;
  padding: 10px;
  margin-bottom: 50px;
}
.navbar_title{
  margin-left: 20px;
}
.navbar_categories a {
  margin-left: 100px;
  margin-right: 20px;
  text-decoration: none;
} 
.todo-task1{
  position: absolute;
  top:3%;
  width:50%;
  height: 100px;
  margin: auto;
  left: 0;
  right: 0;
  text-align: center;
}
.task{
  background-color: #e2e3e3;
    border-radius: 10px;
  width:100%;
  padding: 10px;
  height:100%;
  margin-top:50px;
  margin-bottom: 5px;
  border: 1px solid black;
}
.task .task-content{
  margin: auto;
  left: 0;
  right:0;
  width:90%;
  height:70%;
}
.description{
  margin-top: 10px;
  width:100%;
  height: 60%;
}
.todo-date{
  float: left;
}
#todo-time{
  font-size: 50px;
  position: absolute;
  top:30%;
  right:10%;
}
#date-input{
  margin-left: 15px;
}
#content-button{
  margin-top: 15px;
  float: right;
}
.attach-todo-task {
  width:50%;
  margin-left: 5%;
  border: 1px solid black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f0f0f0;
}
.left {
  flex: 1;
}
.right {
  display: flex;
  align-items: center;
}
.date {
  margin-right: 10px;
}
.right p {
  margin: 0;
  border: 1px solid black;
  padding: 10px;
  margin: 5px;
}
.icons:hover {
  color: red;
  cursor: pointer;
}
.time{
  font-style: italic;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}
#plus-icon{
  width: 60px;
  position:fixed;
  font-size: 50px;
   bottom: 4%;
   right:3%;
   border-radius: 12%;
   border:1px solid black;
}
button:hover{
  color:white;
  background-color: black;
}